import React, { useEffect} from 'react';
import {Link} from 'react-router-dom'
import {Button, Row, Col, ListGroup, Image, Card} from 'react-bootstrap'
import {useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message'
import {CheckoutSteps} from '../components/CheckoutSteps'
import { createOrder } from '../actions/orderAction'


export const PlaceOrderScreen = ({history}) => {
		const dispatch = useDispatch()

		const cart = useSelector((state)=>state.cart)
		/*console.log('Hello Cart Screen')
		console.log(cart)
		console.log(cart.cartItems)
		console.log(cart.shippingAddress)
		console.log(cart.paymentMethod)
		console.log(cart.itemsPrice)
		console.log(cart.shippingPrice)
		console.log(cart.taxPrice)
		console.log(cart.totalPrice)*/

		//Calculate Price

		const addDecimals = (num) => {
			return (Math.round(num * 100)/100).toFixed(2)
		}

		cart.itemsPrice = addDecimals(cart.cartItems.reduce(
			(accumulator, item) => accumulator + item.price * item.quantity,0
			))
		
		cart.shippingPrice = addDecimals(cart.itemsPrice > 100?0:100)
		cart.taxPrice = addDecimals(Number((0.15 * cart.itemsPrice).toFixed(2 )))
		cart.totalPrice = (Number(cart.itemsPrice) + Number(cart.shippingPrice) + Number(cart.taxPrice)).toFixed(2)
		
		//create order state is pulled out from the state
		const orderCreate = useSelector((state)=>state.orderCreate)
		const { order, success, error } = orderCreate
		console.log('Hello PlaceOrderScreen @@@@@@@@@@@@@@@@1')
		console.log(orderCreate)
		console.log(success)
		console.log(order)
		console.log(error)
		console.log('Hello PlaceOrderScreen ...............1')
 
		 
		useEffect(()=>{

			if(success){
				history.push(`/order/${order._id}`)
			}

			// eslint-disable-next-line
		},[ dispatch, success ])

		const placeOrderHandler = () => { 

			 				console.log("HELLO ORDER......................................................................")
				 			console.log(order)
							 
						
							dispatch(createOrder({
								  orderItems: cart.cartItems,
								  shippingAddress: cart.shippingAddress,
								  paymentMethod:cart.paymentMethod,
								  itemsPrice:cart.itemsPrice,
								  taxPrice: cart.taxPrice,
								  shippingPrice: cart.shippingPrice,
								  totalPrice: cart.totalPrice
								}))
					
						
		}
		//payment method is not set in the local storage, so it will lost after reloading it
		return (
			<>
				<CheckoutSteps step1 step2 step3 step4 />
				<Row>
					<Col md={8}>
						<ListGroup variant='flush'>
							<ListGroup.Item>
								<h2>
									Shipping
								</h2>
								<p>
									<strong>Address : </strong>
									{cart.shippingAddress.address},
									{cart.shippingAddress.city},{' '}
									{cart.shippingAddress.postalCode},{' '}
									{cart.shippingAddress.country}
								</p>
							</ListGroup.Item>
							
							<ListGroup.Item>
								<h2>Payment Method</h2>
								<strong>Method: </strong>
								{cart.paymentMethod}
							</ListGroup.Item>

							<ListGroup.Item>
								<h2> Order Items</h2>
								{cart.cartItems.length ===0 ?
								   <Message>Your cart is empty</Message> : (
								   		<ListGroup variant='flush'>
								   			{cart.cartItems.map((item, index)=>(
								   				<ListGroup.Item key={index}>
								   					<Row>
								   						<Col md={1}>
								   							<Image src={item.image} alt={item.name} fluid rounded/>
								   						</Col>
								   						<Col>
								   							<Link to={`/product/${item.product}`}>
								   								{item.name}
								   							</Link>
								   						</Col>	
								   						<Col md={4}>
								   							{item.quantity} x ${item.price} = ${item.quantity * item.price}
								   						</Col>
								   					</Row>	
								   				</ListGroup.Item>
								   				))}
								   		</ListGroup>)
									}
							</ListGroup.Item>
						</ListGroup>
					</Col>
					<Col md={4}>
						<Card>
							<ListGroup variant='flush'>
								<ListGroup.Item>
									<h2>Order Summary</h2>
								</ListGroup.Item>	

								< ListGroup.Item>
									<Row>
										<Col>Items</Col>
										<Col>${cart.itemsPrice}</Col>
									</Row>	
								</ ListGroup.Item>
							
								< ListGroup.Item>
										<Row>
											<Col>Shipping</Col>
											<Col>${cart.shippingPrice}</Col>
										</Row>	
								</ ListGroup.Item>
								
								< ListGroup.Item>
										<Row>
											<Col>Tax</Col>
											<Col>${cart.taxPrice}</Col>
										</Row>	
								</ ListGroup.Item>

								< ListGroup.Item>
										<Row>
											<Col>Total</Col>
											<Col>${cart.totalPrice}</Col>
										</Row>	
								</ ListGroup.Item>

								< ListGroup.Item>
									 {error && <Message variant='danger'>{error}</Message>}
								</ ListGroup.Item>

								
								< ListGroup.Item>
									<Button type='button' 
											className='btn-block' 
											disabled={cart.cartItems===0} 
											onClick={placeOrderHandler}>Place Order</Button>
								</ ListGroup.Item>
							</ListGroup>
						</Card>	

					</Col>
				</Row>			
			</>
		)
	 
}
