import React, {useState, useEffect } from 'react'
import {Row, Col, Card, Image, ListGroup, Button, Form} from 'react-bootstrap'
import {Link} from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import Rating from '../components/Rating'
import Message from '../components/Message'
import Loader from '../components/Loader'
 import {MetaHelmet} from '../components/MetaHelmet'
import { listProductDetails,createProductReview } from '../actions/productAction'
//import axios from 'axios'
import {PRODUCT_CREATE_REVIEW_RESET} from '../constants/productConstant'

const ProductScreen = ({history, match}) =>{//props.history or props.match
	const [quantity,setQuantity] = useState(1) //set number of product
	const [rating,setRating] = useState(0)
	const [comment,setComment] = useState(' ')
	//const [product, setProduct] = useState({})
	const dispatch = useDispatch() //used to send away the actions

	const productDetails = useSelector((state) => state.productDetails)
	const {loading, error, product} = productDetails

	const userLogin = useSelector((state) => state.userLogin)
	const {userInfo} = userLogin
 
 	console.log(userInfo)

 	const productReviewCreate = useSelector((state) => state.productReviewCreate)
	const {
			success:successProductReview,
			error: errorProductReview 
			
		   } = productReviewCreate
 
	useEffect(()=>{
		if(successProductReview){
			alert('Review Submitted!')
			setRating(0)
			setComment('')
			dispatch({ type: PRODUCT_CREATE_REVIEW_RESET})
		}
		dispatch( listProductDetails(match.params.id) )
	}, [dispatch, match, successProductReview]) 

	const addToCartHandler = () => {
		history.push(`/cart/${match.params.id}?quantity=${quantity}`)
	}

	const submitHandler = (event) => {
		event.preventDefault()
		dispatch(createProductReview(match.params.id, {rating, comment }))
	}

	return  <>
				<Link className='btn btn-light my-3' to = '/'>Go Back</Link>
				{loading ? 
					<Loader/>: error ?
					<Message variant='danger'>{error}</Message>:
					(
						<>
						<MetaHelmet title={product.name}/>
						<Row>
							<Col md={6}>
								<Image src={product.image} alt={product.name} fluid/>
							</Col>
							<Col md={3}>
								<ListGroup variant='flush'>
								<ListGroup.Item>
									<h3>{product.name}</h3>
								</ListGroup.Item>
								<ListGroup.Item>
									<Rating 
										value = {product.rating}
										text = {`${product.numReviews} reviews`}
									/>
								</ListGroup.Item>
								<ListGroup.Item>
									Price: ${product.price}
								</ListGroup.Item>
								<ListGroup.Item>
									Description: ${product.description}
								</ListGroup.Item>

								</ListGroup>
							</Col>
							<Col md={3}>
								<Card>
									<ListGroup variant='flush'>
										<ListGroup.Item>
											<Row>
												<Col>
													Price:
												</Col>
												<Col>
													<strong>${product.price}</strong>
												</Col>
											</Row>
										</ListGroup.Item>
										<ListGroup.Item>
											<Row>
												<Col>
													CountInStock:
												</Col>
												<Col>
												 {product.countInStockkey > 0 ? 'In Stock' : 'Out Of Stock'}
												</Col>
											</Row>
										</ListGroup.Item>
										
										{product.countInStockkey > 0 && (
											<ListGroup.Item>
												<Row>
													<Col>Quantity</Col>

													<Col>
														<Form.Control as='select' value={quantity} 
																	  onChange={(event)=>setQuantity(event.target.value)}>

																	 {
																	 	[...Array(product.countInStockkey).keys()].map((x) => (  //javascript for setting product in stock as option 
																	  	<option key={x+1} value={x+1}>  {x+1}  </option>
																	  	))
																	 } 
																	  
														</Form.Control>
													</Col>

												</Row>

											</ListGroup.Item>  
											)}

										<ListGroup.Item>
											 <Button 
											 		onClick = {addToCartHandler}
											 		className='btn-block' 
											 		 type='button' 
											 		 disabled={product.countInStockkey === 0 }>
											 	Add To Cart
											 </Button>
										</ListGroup.Item>
									</ListGroup>
								</Card>
								
							</Col>
						</Row>

						<Row>
						<Col md={6}>
							<h2>Reviews</h2>
							{product.reviews.length === 0 && <Message>No Reviews</Message>}
							<ListGroup variant = 'flush'>
								{product.reviews.map(review=>(
									<ListGroup.Item key={review._id}>
										<strong>{review.name}</strong>
										<Rating value={review.rating}/>
										<p>{review.createdAt.substring(0, 10)}</p>
										<p>{review.comment}</p>
									</ListGroup.Item>

									))}
								<ListGroup.Item>
									<h2>Write a Customer Reviews</h2>
									{errorProductReview && <Message variant='danger'>{errorProductReview}</Message>}
									{userInfo ? (
										<Form onSubmit={submitHandler}>
											<Form.Group controlId='rating'>
												<Form.Label>Rating</Form.Label>
											 	<Form.Control as='select' value={rating} onChange={(event)=>setRating(event.target.value)}>
											 		<option value="">Select...</option>
											 		<option value="1">1 - Poor</option>
											 		<option value="2">2 - Fair</option>
											 		<option value="3">3 - Good</option>
											 		<option value="4">4 - Very Good</option>
											 		<option value="5">5 - Excellent</option>
											 	</Form.Control>
											</Form.Group>
											<Form.Group controlId='comment'>
												<Form.Label>Comment</Form.Label>
												<Form.Control as='textarea'
															  row='3'
															  value={comment}
															  onChange={(event)=>setComment(event.target.value)}></Form.Control>
											</Form.Group>
											<Button type='submit' variant='primary'>Submit</Button>
										</Form>):(
										<Message>
											Please <Link to='/login'>sign in</Link>	to Write review {' '}
										</Message>)}
								</ListGroup.Item>
							</ListGroup>
						</Col>
						</Row>

						</>
					)}
				
			</>


}
export default ProductScreen;